
.solution-list {
  position: relative;

  width: 100%;

  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -ms-box-sizing: border-box;

  padding: 48px 64px 36px 64px;
}
.solution-list-main {
  margin-top: 48px;

  width: 100%;

  display: flex;
  align-items: center;
}
.solution-list-main ul {
  margin: 0;
  padding: 0;

  width: 100%;
  height: 80%;

  list-style: none;

  position: relative;
}
li.solution-list-item {
  width: 15%;

  padding: 16px 0;

  text-align: center;

  color: white;

  transition: background-color 0.3s;
}
li.solution-list-item-active {
  background-color: #0087fb;
}
.solution-list-item-detail {
  position: absolute;

  border-left: 1px solid #eee;

  top: 0;
  left: 15%;

  width: 80%;
  height: 100%;

  visibility: hidden;

  margin-left: 5%;
}
.solution-list-item-active .solution-list-item-detail {
  visibility: visible;
}
.solution-list-bg {
  position: absolute;
  left: 0;
  top: 0;

  width: 100%;
  height: 100%;

  background-position-x: 50%;
  background-size: cover;
  background-position-y: 0;
  background-repeat: no-repeat;

  z-index: -999;

  opacity: 0;

  transition: opacity 0.6s;
}
.solution-list-bg-active {
  opacity: 1;
}
