
.footer {
  width: 100%;
}
.footer-tips {
  position: relative;

  width: 100%;

  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -ms-box-sizing: border-box;
  -webkit-box-sizing: border-box;

  padding: 36px 128px;

  background-size: cover;
  background-position-x: 50%;
  background-position-y: 0;

  background-repeat: no-repeat;

  text-align: center;

  font-size: 24px;

  color: white;
}
.footer-tips button {
  cursor: pointer;
  margin-left: 32px;
  border: 1px solid white;
  font-size: 16px;
  color: white;
  background-color: transparent;
  padding: 12px 48px;
  border-radius: 2px;

  transition: color 0.3s, background-color 0.3s, border-color 0.3s;
}
.footer-tips button:hover {
  background-color: white;
  color: #0087fb;
  border-color: #0087fb;
}
.footer-tips-bg {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: -999;
}
.footer-main {
  width: 100%;

  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -ms-box-sizing: border-box;

  background-color: #2e3033;

  padding: 16px 64px;
}
.footer-main-column {
  width: 100%;

  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -ms-box-sizing: border-box;

  padding: 16px 8px;
}
