
.defence-state {
  width: 100%;

  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -ms-box-sizing: border-box;
  -webkit-box-sizing: border-box;

  padding: 36px 128px;

  background-size: cover;
  background-position-x: 50%;
  background-position-y: 0;

  background-repeat: no-repeat;

  text-align: center;

  font-size: 24px;

  color: white;
}
.defence-state span {
  margin-left: 32px;
  margin-right: 32px;
  font-size: 56px;
  letter-spacing: 16px;
}
