
.swiper-banner-item {
  background-position-x: 50%;
  background-size: cover;
  background-position-y: 0;

  display: flex;

  flex-flow: row wrap;

  box-sizing: border-box;
  -ms-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;

  padding: 80px 120px;
}
.swiper-banner-item-center {
  justify-content: center;
  align-items: center;
}
.swiper-banner-item-center-vertical {
  align-items: center;
}
.swiper-banner-item-center-horizontal {
  justify-content: center;
}
.swiper-banner-item h1 {
  font-size: 48px;
}
.swiper-banner-item p {
  font-size: 18px;
}
.swiper-banner-item button {
  border: 1px solid white;
  background-color: transparent;
  color: white;

  border-radius: 2px;

  margin-top: 16px;

  cursor: pointer;

  font-size: 22px;

  padding: 8px 24px;

  transition: color 0.2s, background-color 0.2s;
}
.swiper-banner-item button:hover {
  color: #2277da;
  background-color: white;
}
.swiper-pagination span.swiper-pagination-bullet {
  background-color: #aaa;
  border-radius: 0;
  width: 12px;
  height: 6px;
}
.swiper-pagination span.swiper-pagination-bullet-active {
  background-color: white;
}
.swiper-button-white {
  opacity: 0;

  transition: opacity 0.2s;
}
.banner-swiper:hover .swiper-button-white {
  opacity: 1;
}
