
.product-list {
  background-size: cover;
  background-position-x: 50%;
  background-position-y: 0;

  width: 100%;

  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -ms-box-sizing: border-box;

  padding: 48px 64px 80px 64px;
}
.product-list-main {
  margin-top: 56px;
  width: 100%;
  height: 500px;
}
